/** @format */
.divbody {
  background-color: white;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  padding: 40px;
}
.annual {
  display: flex;
  justify-content: space-around;
  background-color: rgb(204, 204, 238);
  border-radius: 20px;
  padding: 10px;
  margin: 0% 2%;
  width: 85%;
}
.p1 {
  width: 60%;
  margin-left: 20%;
}
.btn1 {
  width: 90%;
  height: 50px;
  color: white;
  background-color: rgb(17, 17, 247);
  border: none;
  border-radius: 7px;
  margin-top: 20px;
  box-shadow: 10px 10px rgb(247, 244, 244);
}

.ann {
  line-height: 2px;
}

.anco {
  font-weight: bolder;
  margin-top: 10px;
}

@media (min-width: 320px) {
  .p1 {
    width: 80%;
    margin-left: 8%;
  }
  .divbody {
    padding: 20px;
  }
}

@media (min-width: 320px) {
  .annual {
    margin-left: 5%;
  }
}
